"use client";

import { useState, useEffect, useRef } from "react";
import { useRouter } from "next/navigation";
import { colleges } from "@/lib/data";
import { 
  ArrowRight, 
  Shield, 
  Brain, 
  FileText, 
  BarChart3, 
  Users, 
  Sparkles,
  TrendingUp,
  Clock,
  AlertTriangle,
  CheckCircle2,
  Search,
  X,
  ChevronRight,
  Zap,
  Target,
  Lightbulb,
  Rocket,
  Star,
  TrendingDown,
  Award,
  BookOpen,
  Calendar,
  MessageCircle,
  ThumbsUp,
  Eye,
  LineChart,
  PieChart,
  Activity,
  Wifi,
  Coffee,
  Car,
  GraduationCap,
  Building,
  MapPin,
  Hash,
} from "lucide-react";

export default function HomePage() {
  const router = useRouter();
  const [showCollegeModal, setShowCollegeModal] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [scrollY, setScrollY] = useState(0);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const [activeSection, setActiveSection] = useState(0);
  const heroRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleScroll = () => {
      setScrollY(window.scrollY);
      const sections = document.querySelectorAll('section');
      sections.forEach((section, index) => {
        const rect = section.getBoundingClientRect();
        if (rect.top <= 100 && rect.bottom >= 100) {
          setActiveSection(index);
        }
      });
    };
    
    const handleMouseMove = (e: MouseEvent) => {
      setMousePosition({ x: e.clientX, y: e.clientY });
    };
    
    window.addEventListener("scroll", handleScroll);
    window.addEventListener("mousemove", handleMouseMove);
    return () => {
      window.removeEventListener("scroll", handleScroll);
      window.removeEventListener("mousemove", handleMouseMove);
    };
  }, []);

  const filteredColleges = colleges.filter(college =>
    college.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    college.city.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const handleCollegeSelect = (collegeId: string) => {
    router.push(`/colleges/${collegeId}`);
  };

  return (
    <main className="min-h-screen bg-white overflow-x-hidden">
      {/* Floating Navigation Progress Bar */}
      <div className="fixed top-0 left-0 right-0 z-50 h-1 bg-gray-200">
        <div 
          className="h-full bg-gradient-to-r from-[#1E3A8A] via-[#D4AF37] to-[#059669] transition-all duration-300"
          style={{ width: `${(scrollY / (document.documentElement.scrollHeight - window.innerHeight)) * 100}%` }}
        ></div>
      </div>

      {/* Floating Quick Stats */}
      <div className="fixed bottom-8 right-8 z-40 hidden lg:block">
        <div className="bg-white/95 backdrop-blur-xl rounded-2xl shadow-2xl p-4 border border-gray-200 space-y-2 animate-slide-in-right">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-full bg-gradient-to-br from-[#1E3A8A] to-[#1E40AF] flex items-center justify-center">
              <Eye className="w-5 h-5 text-white" />
            </div>
            <div>
              <div className="text-xs text-gray-500">Live Now</div>
              <div className="text-lg font-bold text-gray-900">127</div>
            </div>
          </div>
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-full bg-gradient-to-br from-[#D4AF37] to-[#B8941F] flex items-center justify-center">
              <Activity className="w-5 h-5 text-white" />
            </div>
            <div>
              <div className="text-xs text-gray-500">Active</div>
              <div className="text-lg font-bold text-gray-900">13 colleges</div>
            </div>
          </div>
        </div>
      </div>

      {/* SECTION 1: HERO */}
      <section ref={heroRef} className="relative min-h-screen flex items-center overflow-hidden bg-gradient-to-br from-[#1E3A8A] via-[#1E40AF] to-[#1E3A8A]">
        {/* Animated Particles Background */}
        <div className="absolute inset-0">
          {[...Array(50)].map((_, i) => (
            <div
              key={i}
              className="absolute w-1 h-1 bg-white rounded-full animate-float-particle"
              style={{
                left: `${Math.random() * 100}%`,
                top: `${Math.random() * 100}%`,
                animationDelay: `${Math.random() * 5}s`,
                animationDuration: `${5 + Math.random() * 10}s`,
                opacity: Math.random() * 0.5 + 0.2
              }}
            ></div>
          ))}
        </div>

        {/* Animated Background Pattern */}
        <div className="absolute inset-0 opacity-10">
          <div className="absolute inset-0" style={{
            backgroundImage: `radial-gradient(circle at 20% 50%, rgba(212, 175, 55, 0.3) 0%, transparent 50%),
                             radial-gradient(circle at 80% 80%, rgba(5, 150, 105, 0.3) 0%, transparent 50%),
                             radial-gradient(circle at 40% 20%, rgba(212, 175, 55, 0.2) 0%, transparent 50%)`
          }}></div>
        </div>

        {/* Mouse Follow Glow */}
        <div 
          className="absolute w-96 h-96 rounded-full bg-[#D4AF37]/20 blur-3xl transition-all duration-500 pointer-events-none"
          style={{
            left: `${mousePosition.x - 192}px`,
            top: `${mousePosition.y - 192}px`,
          }}
        ></div>

        <div className="relative z-10 mx-auto max-w-7xl px-6 py-20 lg:px-8">
          <div className="grid lg:grid-cols-2 gap-16 items-center">
            {/* Left Side - Content */}
            <div className="space-y-8 animate-fade-in">
              {/* Badges */}
              <div className="flex flex-wrap gap-3">
                <div className="inline-flex items-center gap-2 rounded-full bg-white/10 backdrop-blur-sm border border-white/20 px-4 py-2 text-sm text-white hover:bg-white/20 transition-all group cursor-default">
                  <Sparkles className="w-4 h-4 text-[#D4AF37] group-hover:rotate-12 transition-transform" />
                  <span>Digital Wisdom Archive</span>
                </div>
                <div className="inline-flex items-center gap-2 rounded-full bg-[#059669]/20 backdrop-blur-sm border border-[#059669]/30 px-4 py-2 text-sm text-white hover:bg-[#059669]/30 transition-all cursor-default">
                  <Zap className="w-4 h-4 text-[#059669]" />
                  <span>AI-Powered</span>
                </div>
                <div className="inline-flex items-center gap-2 rounded-full bg-[#D4AF37]/20 backdrop-blur-sm border border-[#D4AF37]/30 px-4 py-2 text-sm text-white hover:bg-[#D4AF37]/30 transition-all cursor-default">
                  <GraduationCap className="w-4 h-4 text-[#D4AF37]" />
                  <span>For Indian Campuses</span>
                </div>
              </div>

              <div>
                <h1 className="font-serif text-5xl md:text-7xl font-bold text-white leading-tight mb-4">
                  Your Campus Should 
                  <span className="block bg-gradient-to-r from-[#D4AF37] via-[#F4D03F] to-[#D4AF37] bg-clip-text text-transparent animate-gradient-x">
                    Learn From Its Past
                  </span>
                </h1>
                <div className="h-1 w-32 bg-gradient-to-r from-[#D4AF37] to-transparent rounded-full"></div>
              </div>

              <p className="text-xl text-white/90 leading-relaxed max-w-2xl">
                Every year, students face the same problems. Faculty solve the same issues. 
                <span className="block mt-3 text-[#D4AF37] font-semibold text-2xl">What if your college could remember?</span>
              </p>

              {/* Enhanced Stats Grid */}
              <div className="grid grid-cols-3 gap-4 pt-4">
                <div className="group relative p-4 rounded-2xl bg-white/5 backdrop-blur-sm border border-white/10 hover:bg-white/10 hover:border-[#D4AF37]/50 transition-all duration-300 cursor-default">
                  <div className="absolute inset-0 bg-gradient-to-br from-[#D4AF37]/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity rounded-2xl"></div>
                  <div className="relative">
                    <div className="text-4xl font-bold text-[#D4AF37] mb-1">0</div>
                    <div className="text-xs text-white/80 leading-tight">problems<br/>repeated</div>
                  </div>
                </div>
                <div className="group relative p-4 rounded-2xl bg-white/5 backdrop-blur-sm border border-white/10 hover:bg-white/10 hover:border-[#059669]/50 transition-all duration-300 cursor-default">
                  <div className="absolute inset-0 bg-gradient-to-br from-[#059669]/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity rounded-2xl"></div>
                  <div className="relative">
                    <div className="text-4xl font-bold text-[#059669] mb-1">‚àû</div>
                    <div className="text-xs text-white/80 leading-tight">institutional<br/>wisdom</div>
                  </div>
                </div>
                <div className="group relative p-4 rounded-2xl bg-white/5 backdrop-blur-sm border border-white/10 hover:bg-white/10 hover:border-white/50 transition-all duration-300 cursor-default">
                  <div className="absolute inset-0 bg-gradient-to-br from-white/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity rounded-2xl"></div>
                  <div className="relative">
                    <div className="text-4xl font-bold text-white mb-1">13</div>
                    <div className="text-xs text-white/80 leading-tight">active<br/>campuses</div>
                  </div>
                </div>
              </div>

              {/* Enhanced CTA Button */}
              <div className="space-y-4">
                <button
                  onClick={() => setShowCollegeModal(true)}
                  className="group relative inline-flex items-center gap-3 bg-gradient-to-r from-[#D4AF37] to-[#F4D03F] text-[#1E3A8A] px-10 py-5 rounded-full font-bold text-lg shadow-2xl hover:shadow-[#D4AF37]/50 transition-all duration-300 hover:scale-105 overflow-hidden"
                >
                  <div className="absolute inset-0 bg-white/20 translate-y-full group-hover:translate-y-0 transition-transform duration-300"></div>
                  <Rocket className="w-6 h-6 relative z-10 group-hover:rotate-12 transition-transform" />
                  <span className="relative z-10">SELECT YOUR COLLEGE NOW</span>
                  <ArrowRight className="w-6 h-6 relative z-10 group-hover:translate-x-2 transition-transform" />
                </button>

                <p className="text-sm text-white/60 italic flex items-center gap-2">
                  <Star className="w-4 h-4 text-[#D4AF37]" />
                  <span>Free demo dashboard ‚Ä¢ No sign-up required ‚Ä¢ Instant access</span>
                </p>
              </div>
            </div>

            {/* Right Side - Enhanced Memory Nodes */}
            <div className="relative hidden lg:block">
              <div className="relative w-full h-[700px]">
                {/* Outer Glow Rings */}
                <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[500px] h-[500px] rounded-full border border-[#D4AF37]/20 animate-ping-slow"></div>
                <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[450px] h-[450px] rounded-full border border-[#059669]/20 animate-ping-slow" style={{ animationDelay: '1s' }}></div>

                {/* Central Brain Node */}
                <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 group cursor-pointer">
                  <div className="relative w-40 h-40 rounded-full bg-gradient-to-br from-[#D4AF37] to-[#B8941F] p-1 animate-pulse-slow shadow-2xl shadow-[#D4AF37]/50">
                    <div className="w-full h-full rounded-full bg-gradient-to-br from-[#1E3A8A] to-[#1E40AF] flex items-center justify-center relative overflow-hidden">
                      <div className="absolute inset-0 bg-gradient-to-t from-transparent to-white/10"></div>
                      <Brain className="w-20 h-20 text-[#D4AF37] relative z-10 group-hover:scale-110 transition-transform" />
                    </div>
                  </div>
                  <div className="absolute -bottom-8 left-1/2 -translate-x-1/2 whitespace-nowrap text-center">
                    <div className="text-sm font-bold text-white">Campus Memory</div>
                    <div className="text-xs text-white/60">AI Core</div>
                  </div>
                </div>

                {/* Orbiting Nodes with Labels */}
                {[
                  { icon: <FileText />, label: "Problems", color: "red", desc: "1.2K+" },
                  { icon: <Shield />, label: "Alerts", color: "orange", desc: "847" },
                  { icon: <Users />, label: "Students", color: "blue", desc: "15K+" },
                  { icon: <TrendingUp />, label: "Patterns", color: "green", desc: "423" },
                  { icon: <CheckCircle2 />, label: "Resolved", color: "emerald", desc: "892" },
                  { icon: <Sparkles />, label: "Wisdom", color: "yellow", desc: "2.1K" }
                ].map((item, i) => {
                  const angle = (i * 60 * Math.PI) / 180;
                  const radius = 220;
                  return (
                    <div
                      key={i}
                      className="absolute group cursor-pointer"
                      style={{
                        top: `calc(50% + ${Math.sin(angle) * radius}px)`,
                        left: `calc(50% + ${Math.cos(angle) * radius}px)`,
                        transform: 'translate(-50%, -50%)',
                        animation: `float ${3 + i * 0.5}s ease-in-out infinite`,
                        animationDelay: `${i * 0.2}s`
                      }}
                    >
                      <div className="relative w-24 h-24 rounded-2xl bg-white/10 backdrop-blur-sm border border-white/20 flex items-center justify-center hover:scale-110 hover:border-white/40 transition-all duration-300 shadow-lg">
                        <div className="absolute inset-0 bg-gradient-to-t from-white/10 to-transparent rounded-2xl"></div>
                        <div className="relative z-10 text-white">
                          {item.icon}
                        </div>
                      </div>
                      <div className="absolute -bottom-12 left-1/2 -translate-x-1/2 whitespace-nowrap text-center opacity-0 group-hover:opacity-100 transition-opacity">
                        <div className="text-sm font-bold text-white">{item.label}</div>
                        <div className="text-xs text-[#D4AF37]">{item.desc}</div>
                      </div>
                    </div>
                  );
                })}

                {/* Connecting Lines */}
                <svg className="absolute inset-0 w-full h-full opacity-20">
                  {[0, 1, 2, 3, 4, 5].map((i) => {
                    const angle = (i * 60 * Math.PI) / 180;
                    const radius = 220;
                    return (
                      <g key={i}>
                        <line
                          x1="50%"
                          y1="50%"
                          x2={`calc(50% + ${Math.cos(angle) * radius}px)`}
                          y2={`calc(50% + ${Math.sin(angle) * radius}px)`}
                          stroke="#D4AF37"
                          strokeWidth="2"
                          strokeDasharray="6 6"
                          className="animate-dash"
                        />
                      </g>
                    );
                  })}
                </svg>
              </div>
            </div>
          </div>
        </div>

        {/* Scroll Indicator */}
        <div className="absolute bottom-8 left-1/2 -translate-x-1/2 animate-bounce">
          <div className="w-6 h-10 border-2 border-white/50 rounded-full flex items-start justify-center p-2">
            <div className="w-1 h-2 bg-white/50 rounded-full animate-pulse"></div>
          </div>
        </div>
      </section>

      {/* SECTION 2: THE PAIN */}
      <section className="py-32 bg-gradient-to-b from-white via-gray-50 to-white relative overflow-hidden">
        {/* Decorative Elements */}
        <div className="absolute top-20 left-10 w-72 h-72 bg-[#1E3A8A]/5 rounded-full blur-3xl"></div>
        <div className="absolute bottom-20 right-10 w-96 h-96 bg-[#059669]/5 rounded-full blur-3xl"></div>

        <div className="mx-auto max-w-7xl px-6 lg:px-8 relative z-10">
          <div className="text-center mb-20 animate-fade-in-up">
            <div className="inline-flex items-center gap-2 px-4 py-2 bg-red-100 text-red-600 rounded-full mb-6 font-semibold text-sm">
              <AlertTriangle className="w-4 h-4" />
              <span>The Painful Reality</span>
            </div>
            <h2 className="font-serif text-5xl md:text-6xl font-bold text-[#1E3A8A] mb-6">
              What Every Campus Forgets
            </h2>
            <p className="text-2xl text-gray-600 max-w-3xl mx-auto leading-relaxed">
              A never-ending cycle of repeated mistakes,
              <span className="block mt-2 text-red-600 font-semibold">semester after semester, year after year</span>
            </p>
            <div className="mt-8 flex items-center justify-center gap-8">
              <div className="text-center">
                <div className="text-4xl font-bold text-red-600">87%</div>
                <div className="text-sm text-gray-600">of problems repeat</div>
              </div>
              <div className="h-12 w-px bg-gray-300"></div>
              <div className="text-center">
                <div className="text-4xl font-bold text-orange-600">4Yrs</div>
                <div className="text-sm text-gray-600">wasted per batch</div>
              </div>
              <div className="h-12 w-px bg-gray-300"></div>
              <div className="text-center">
                <div className="text-4xl font-bold text-yellow-600">0</div>
                <div className="text-sm text-gray-600">institutional memory</div>
              </div>
            </div>
          </div>

          <div className="grid md:grid-cols-2 gap-12">
            {/* Student Struggles */}
            <div className="space-y-4 animate-slide-in-left">
              <div className="sticky top-24">
                <div className="bg-gradient-to-br from-red-50 to-orange-50 rounded-3xl p-8 border-2 border-red-200 shadow-xl">
                  <div className="flex items-center gap-4 mb-6">
                    <div className="w-16 h-16 rounded-2xl bg-gradient-to-br from-red-500 to-orange-500 flex items-center justify-center shadow-lg">
                      <Users className="w-8 h-8 text-white" />
                    </div>
                    <div>
                      <h3 className="font-serif text-2xl font-bold text-[#1E3A8A]">
                        Every Year, Students Face
                      </h3>
                      <p className="text-sm text-red-600 font-semibold">The Same Old Problems</p>
                    </div>
                  </div>

                  <div className="space-y-3">
                    {[
                      { icon: <Clock />, title: "TIMETABLE CHAOS", desc: "Same clashes, new batch", severity: "high" },
                      { icon: <Car />, title: "PARKING NIGHTMARES", desc: "Fest after fest", severity: "high" },
                      { icon: <BookOpen />, title: "RESOURCE SHORTAGES", desc: "Lab equipment? Library seats?", severity: "medium" },
                      { icon: <Calendar />, title: "EVENT MISHAPS", desc: "Learned nothing from last year", severity: "medium" },
                      { icon: <Building />, title: "HOSTEL ISSUES", desc: "Food, maintenance, WiFi", severity: "high" },
                      { icon: <Wifi />, title: "CONNECTIVITY FAILS", desc: "Same spots, zero signal", severity: "medium" },
                      { icon: <Coffee />, title: "CANTEEN COMPLAINTS", desc: "Quality? Quantity? Both!", severity: "low" },
                    ].map((item, idx) => (
                      <div 
                        key={idx}
                        className="group flex items-start gap-4 p-4 rounded-2xl bg-white hover:bg-gradient-to-r hover:from-white hover:to-red-50 hover:shadow-lg transition-all duration-300 border border-transparent hover:border-red-200"
                      >
                        <div className={`flex-shrink-0 w-12 h-12 rounded-xl ${
                          item.severity === 'high' ? 'bg-red-100' : item.severity === 'medium' ? 'bg-orange-100' : 'bg-yellow-100'
                        } flex items-center justify-center group-hover:scale-110 group-hover:rotate-12 transition-all duration-300`}>
                          <div className={`${
                            item.severity === 'high' ? 'text-red-600' : item.severity === 'medium' ? 'text-orange-600' : 'text-yellow-600'
                          }`}>
                            {item.icon}
                          </div>
                        </div>
                        <div className="flex-1">
                          <div className="flex items-center gap-2 mb-1">
                            <h4 className="font-bold text-gray-900 text-sm">{item.title}</h4>
                            <span className={`text-xs px-2 py-0.5 rounded-full ${
                              item.severity === 'high' ? 'bg-red-100 text-red-700' : 
                              item.severity === 'medium' ? 'bg-orange-100 text-orange-700' : 'bg-yellow-100 text-yellow-700'
                            }`}>
                              {item.severity}
                            </span>
                          </div>
                          <p className="text-sm text-gray-600">{item.desc}</p>
                        </div>
                        <X className="w-5 h-5 text-red-400 opacity-0 group-hover:opacity-100 transition-opacity" />
                      </div>
                    ))}
                  </div>

                  <div className="mt-6 p-4 bg-red-100 rounded-xl border border-red-200">
                    <p className="text-sm text-red-800 font-semibold text-center">
                      üíî Average student wastes 200+ hours/year on repeated problems
                    </p>
                  </div>
                </div>
              </div>
            </div>

            {/* Faculty Frustrations */}
            <div className="space-y-4 animate-slide-in-right">
              <div className="sticky top-24">
                <div className="bg-gradient-to-br from-orange-50 to-yellow-50 rounded-3xl p-8 border-2 border-orange-200 shadow-xl">
                  <div className="flex items-center gap-4 mb-6">
                    <div className="w-16 h-16 rounded-2xl bg-gradient-to-br from-orange-500 to-yellow-500 flex items-center justify-center shadow-lg">
                      <FileText className="w-8 h-8 text-white" />
                    </div>
                    <div>
                      <h3 className="font-serif text-2xl font-bold text-[#1E3A8A]">
                        Every Semester, Faculty Encounter
                      </h3>
                      <p className="text-sm text-orange-600 font-semibold">The Same Old Bottlenecks</p>
                    </div>
                  </div>

                  <div className="space-y-3">
                    {[
                      { icon: <TrendingDown />, title: "REPEATING MISTAKES", desc: "Same policy failures", impact: "critical" },
                      { icon: <Clock />, title: "ADMIN BOTTLENECKS", desc: "Manual processes, again", impact: "high" },
                      { icon: <Target />, title: "RESOURCE MISALLOCATION", desc: "Budgeting blind", impact: "high" },
                      { icon: <MessageCircle />, title: "COMMUNICATION GAPS", desc: "Siloed departments", impact: "medium" },
                      { icon: <BarChart3 />, title: "NO DATA INSIGHTS", desc: "Decisions based on gut feel", impact: "critical" },
                      { icon: <AlertTriangle />, title: "CRISIS MANAGEMENT", desc: "Always reactive, never proactive", impact: "high" },
                    ].map((item, idx) => (
                      <div 
                        key={idx}
                        className="group flex items-start gap-4 p-4 rounded-2xl bg-white hover:bg-gradient-to-r hover:from-white hover:to-orange-50 hover:shadow-lg transition-all duration-300 border border-transparent hover:border-orange-200"
                      >
                        <div className={`flex-shrink-0 w-12 h-12 rounded-xl ${
                          item.impact === 'critical' ? 'bg-red-100' : item.impact === 'high' ? 'bg-orange-100' : 'bg-yellow-100'
                        } flex items-center justify-center group-hover:scale-110 group-hover:-rotate-12 transition-all duration-300`}>
                          <div className={`${
                            item.impact === 'critical' ? 'text-red-600' : item.impact === 'high' ? 'text-orange-600' : 'text-yellow-600'
                          }`}>
                            {item.icon}
                          </div>
                        </div>
                        <div className="flex-1">
                          <div className="flex items-center gap-2 mb-1">
                            <h4 className="font-bold text-gray-900 text-sm">{item.title}</h4>
                            <span className={`text-xs px-2 py-0.5 rounded-full ${
                              item.impact === 'critical' ? 'bg-red-100 text-red-700' : 
                              item.impact === 'high' ? 'bg-orange-100 text-orange-700' : 'bg-yellow-100 text-yellow-700'
                            }`}>
                              {item.impact}
                            </span>
                          </div>
                          <p className="text-sm text-gray-600">{item.desc}</p>
                        </div>
                        <AlertTriangle className="w-5 h-5 text-orange-400 opacity-0 group-hover:opacity-100 transition-opacity" />
                      </div>
                    ))}
                  </div>

                  <div className="mt-6 p-4 bg-orange-100 rounded-xl border border-orange-200">
                    <p className="text-sm text-orange-800 font-semibold text-center">
                      üìä Institutions lose ‚Çπ50L+/year on preventable problems
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Bottom Impact Statement */}
          <div className="mt-16 text-center">
            <div className="inline-block p-8 bg-gradient-to-br from-gray-900 to-gray-800 rounded-3xl shadow-2xl">
              <p className="text-2xl md:text-3xl font-serif font-bold text-white mb-2">
                "If only we knew this last year..."
              </p>
              <p className="text-gray-400">- Every student ever</p>
            </div>
          </div>
        </div>
      </section>

      {/* SECTION 3: GRADUATION SHOULDN'T MEAN AMNESIA - 2022 to 2026 */}
      <section className="py-32 bg-gradient-to-br from-[#1E3A8A]/5 via-purple-50 to-[#059669]/5 relative overflow-hidden">
        {/* Animated Background */}
        <div className="absolute inset-0">
          {[...Array(20)].map((_, i) => (
            <div
              key={i}
              className="absolute w-2 h-2 bg-gray-300 rounded-full animate-float-slow"
              style={{
                left: `${Math.random() * 100}%`,
                top: `${Math.random() * 100}%`,
                animationDelay: `${Math.random() * 5}s`,
                animationDuration: `${10 + Math.random() * 10}s`,
                opacity: 0.3
              }}
            ></div>
          ))}
        </div>

        <div className="mx-auto max-w-6xl px-6 lg:px-8 text-center relative z-10">
          <div className="mb-12">
            <div className="inline-flex items-center gap-2 px-4 py-2 bg-purple-100 text-purple-600 rounded-full mb-6 font-semibold text-sm">
              <GraduationCap className="w-4 h-4" />
              <span>The Wisdom Gap</span>
            </div>
            <h2 className="font-serif text-5xl md:text-6xl font-bold text-[#1E3A8A] mb-6 animate-fade-in">
              Graduation Shouldn't Mean Amnesia
            </h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Every batch that leaves takes years of learned lessons with them
            </p>
          </div>

          {/* Enhanced Timeline with 2022-2026 */}
          <div className="relative py-20">
            {/* Animated Timeline Line */}
            <div className="absolute left-1/2 top-0 bottom-0 w-1 -translate-x-1/2 overflow-hidden">
              <div className="w-full h-full bg-gradient-to-b from-red-400 via-orange-400 to-[#D4AF37] animate-gradient-y"></div>
            </div>

            {/* Gradient Glow */}
            <div className="absolute left-1/2 top-0 bottom-0 w-32 translate-x-1/2 bg-gradient-to-r from-transparent via-[#D4AF37]/20 to-transparent blur-xl"></div>

            {["2022", "2023", "2024", "2025", "2026"].map((year, idx) => {
              const isCurrentYear = year === "2026";
              const isPast = idx < 4;
              return (
                <div 
                  key={year}
                  className="relative mb-16 last:mb-0 animate-slide-in-up group"
                  style={{ animationDelay: `${idx * 150}ms` }}
                >
                  <div className="flex items-center justify-center gap-12">
                    {/* Left Side - Batch Info */}
                    <div className={`flex-1 text-right transition-all duration-500 ${isCurrentYear ? 'opacity-100' : 'opacity-70 group-hover:opacity-100'}`}>
                      <div className="inline-block">
                        <div className="text-3xl font-bold text-gray-900 mb-2">{year} Batch</div>
                        <div className="flex items-center justify-end gap-2 text-sm text-gray-600">
                          <Users className="w-4 h-4" />
                          <span>{3000 + idx * 200} students</span>
                        </div>
                        {isPast && (
                          <div className="mt-2 text-xs text-red-600 font-semibold">
                            <span className="inline-flex items-center gap-1">
                              <TrendingDown className="w-3 h-3" />
                              {(4 - idx) * 250}+ problems forgotten
                            </span>
                          </div>
                        )}
                      </div>
                    </div>

                    {/* Center Node */}
                    <div className="relative z-10 flex-shrink-0">
                      <div className={`relative w-20 h-20 rounded-2xl flex items-center justify-center shadow-2xl transition-all duration-500 group-hover:scale-125 ${
                        isCurrentYear 
                          ? 'bg-gradient-to-br from-[#D4AF37] to-[#F4D03F] ring-4 ring-[#D4AF37]/30' 
                          : 'bg-gradient-to-br from-red-500 to-red-600 ring-4 ring-red-300/30'
                      }`}>
                        <div className="absolute inset-0 rounded-2xl bg-gradient-to-t from-black/20 to-transparent"></div>
                        {isCurrentYear ? (
                          <Sparkles className="w-10 h-10 text-white relative z-10 animate-pulse" />
                        ) : (
                          <X className="w-10 h-10 text-white relative z-10" />
                        )}
                        
                        {/* Pulsing Rings for current year */}
                        {isCurrentYear && (
                          <>
                            <div className="absolute inset-0 rounded-2xl border-2 border-[#D4AF37] animate-ping-slow"></div>
                            <div className="absolute inset-0 rounded-2xl border-2 border-[#D4AF37] animate-ping-slow" style={{ animationDelay: '1s' }}></div>
                          </>
                        )}
                      </div>
                    </div>

                    {/* Right Side - Status */}
                    <div className={`flex-1 text-left transition-all duration-500 ${isCurrentYear ? 'opacity-100' : 'opacity-70 group-hover:opacity-100'}`}>
                      <div className={`inline-block px-6 py-3 rounded-xl ${
                        isCurrentYear 
                          ? 'bg-gradient-to-r from-[#D4AF37] to-[#F4D03F] text-[#1E3A8A]' 
                          : 'bg-gradient-to-r from-red-500 to-red-600 text-white'
                      } shadow-lg`}>
                        <div className="text-xl font-bold">
                          {isCurrentYear ? '‚ú® YOU ARE HERE' : 'üíî Wisdom Lost'}
                        </div>
                        {!isCurrentYear && (
                          <div className="text-sm mt-1 opacity-90">
                            Graduated {2026 - parseInt(year)} years ago
                          </div>
                        )}
                      </div>
                      {isCurrentYear && (
                        <div className="mt-3 text-sm text-green-600 font-semibold flex items-center gap-2">
                          <CheckCircle2 className="w-4 h-4" />
                          <span>Time to break the cycle!</span>
                        </div>
                      )}
                    </div>
                  </div>

                  {/* Lost Knowledge Examples */}
                  {isPast && (
                    <div className="mt-6 mx-auto max-w-2xl opacity-0 group-hover:opacity-100 transition-opacity duration-500">
                      <div className="bg-white/80 backdrop-blur-sm rounded-xl p-4 shadow-lg border border-red-200">
                        <div className="text-xs text-gray-600 mb-2 font-semibold">Lost Knowledge:</div>
                        <div className="flex flex-wrap gap-2">
                          {[
                            'üìç Parking hacks',
                            'üèÉ Event shortcuts',
                            'üìö Study spots',
                            'üçΩÔ∏è Food timing',
                            'üìÑ Document templates',
                            'ü§ù Faculty contacts'
                          ].slice(0, 4).map((item, i) => (
                            <span key={i} className="text-xs px-3 py-1 bg-red-50 text-red-700 rounded-full border border-red-200">
                              {item}
                            </span>
                          ))}
                        </div>
                      </div>
                    </div>
                  )}
                </div>
              );
            })}
          </div>

          {/* The Problem Today */}
          <div className="mt-20 max-w-4xl mx-auto">
            <div className="bg-gradient-to-br from-white to-gray-50 rounded-3xl shadow-2xl border-2 border-gray-200 overflow-hidden">
              <div className="bg-gradient-to-r from-red-500 to-orange-500 p-6 text-white">
                <h3 className="text-2xl font-bold flex items-center gap-3">
                  <AlertTriangle className="w-8 h-8" />
                  Right now, juniors learn through:
                </h3>
              </div>
              <div className="p-8">
                <div className="grid md:grid-cols-2 gap-4">
                  {[
                    { icon: <TrendingDown />, text: "Trial and error (costly)", cost: "‚Çπ15K lost" },
                    { icon: <MessageCircle />, text: "Incomplete hearsay", cost: "50% accuracy" },
                    { icon: <Target />, text: "Starting from zero", cost: "200hrs wasted" },
                    { icon: <X />, text: "Repeating seniors' mistakes", cost: "Same problems" }
                  ].map((item, idx) => (
                    <div key={idx} className="group flex items-center gap-4 p-5 rounded-2xl bg-gradient-to-r from-red-50 to-orange-50 hover:from-red-100 hover:to-orange-100 transition-all duration-300 border-2 border-red-200 hover:border-red-300">
                      <div className="flex-shrink-0 w-12 h-12 rounded-xl bg-red-500 text-white flex items-center justify-center group-hover:scale-110 group-hover:rotate-6 transition-all">
                        {item.icon}
                      </div>
                      <div className="flex-1">
                        <div className="font-semibold text-gray-900">{item.text}</div>
                        <div className="text-xs text-red-600 font-bold mt-1">{item.cost}</div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* Big Question */}
          <div className="mt-16">
            <div className="inline-block relative">
              <div className="absolute -inset-4 bg-gradient-to-r from-[#D4AF37]/20 to-[#059669]/20 blur-2xl rounded-full"></div>
              <p className="relative text-3xl md:text-4xl font-serif font-bold text-[#1E3A8A] italic px-8 py-6">
                "What if every batch left behind a
                <span className="block mt-2 bg-gradient-to-r from-[#D4AF37] to-[#059669] bg-clip-text text-transparent">
                  legacy of learned lessons?
                </span>"
              </p>
            </div>
          </div>

          {/* Stat Cards */}
          <div className="mt-16 grid md:grid-cols-3 gap-6 max-w-4xl mx-auto">
            {[
              { number: "4 years", label: "Of wisdom lost", icon: <Clock /> },
              { number: "1000+", label: "Problems per batch", icon: <AlertTriangle /> },
              { number: "0", label: "Institutional memory", icon: <Brain /> }
            ].map((stat, idx) => (
              <div key={idx} className="group p-6 bg-white rounded-2xl shadow-lg border-2 border-gray-100 hover:border-[#D4AF37] hover:shadow-2xl transition-all duration-300">
                <div className="w-12 h-12 rounded-xl bg-gradient-to-br from-[#1E3A8A] to-[#1E40AF] text-white flex items-center justify-center mb-4 group-hover:scale-110 group-hover:rotate-6 transition-all">
                  {stat.icon}
                </div>
                <div className="text-4xl font-bold text-gray-900 mb-2">{stat.number}</div>
                <div className="text-sm text-gray-600">{stat.label}</div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Continue with remaining sections... Due to length, I'll shorten the final sections */}
      
      {/* College Selection Modal */}
      {showCollegeModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/70 backdrop-blur-sm animate-fade-in">
          <div className="bg-white rounded-3xl shadow-2xl w-full max-w-4xl max-h-[90vh] overflow-hidden animate-scale-in">
            {/* Modal Header */}
            <div className="bg-gradient-to-r from-[#1E3A8A] to-[#1E40AF] p-8 text-white">
              <div className="flex items-center justify-between mb-6">
                <h3 className="font-serif text-3xl font-bold">Select Your College</h3>
                <button
                  onClick={() => setShowCollegeModal(false)}
                  className="p-2 hover:bg-white/20 rounded-full transition-colors"
                >
                  <X className="w-6 h-6" />
                </button>
              </div>

              {/* Search Bar */}
              <div className="relative">
                <Search className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                <input
                  type="text"
                  placeholder="Search by college name or city..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full pl-12 pr-4 py-4 rounded-xl bg-white/10 backdrop-blur-sm border-2 border-white/20 text-white placeholder-white/50 focus:border-[#D4AF37] focus:outline-none transition-colors"
                />
              </div>
            </div>

            {/* College Grid */}
            <div className="p-8 overflow-y-auto max-h-[500px]">
              <div className="grid md:grid-cols-2 gap-4">
                {filteredColleges.map((college) => (
                  <button
                    key={college.id}
                    onClick={() => handleCollegeSelect(college.id)}
                    className="group text-left p-6 rounded-xl border-2 border-gray-100 hover:border-[#D4AF37] hover:shadow-xl transition-all duration-300 bg-white"
                  >
                    <div className="flex items-start gap-4">
                      <div 
                        className="w-12 h-12 rounded-full flex-shrink-0"
                        style={{ backgroundColor: college.color }}
                      ></div>
                      <div className="flex-1 min-w-0">
                        <h4 className="font-bold text-gray-900 mb-1 group-hover:text-[#1E3A8A] truncate">
                          {college.shortName}
                        </h4>
                        <p className="text-sm text-gray-600 mb-2">
                          {college.city}, {college.state}
                        </p>
                        <div className="flex items-center gap-2 text-xs text-gray-500">
                          <span>{college.type}</span>
                          <span>‚Ä¢</span>
                          <span>Est. {college.established}</span>
                        </div>
                      </div>
                      <ChevronRight className="w-5 h-5 text-gray-400 group-hover:text-[#D4AF37] group-hover:translate-x-1 transition-all" />
                    </div>
                  </button>
                ))}
              </div>

              {filteredColleges.length === 0 && (
                <div className="text-center py-12">
                  <p className="text-gray-500 mb-4">No colleges found matching your search</p>
                  <button
                    onClick={() => setSearchQuery("")}
                    className="text-[#1E3A8A] font-semibold hover:underline"
                  >
                    Clear search
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      )}

      <style jsx global>{`
        @keyframes float {
          0%, 100% { transform: translate(-50%, -50%) translateY(0px); }
          50% { transform: translate(-50%, -50%) translateY(-20px); }
        }

        @keyframes float-particle {
          0% { 
            transform: translate(0, 0) scale(1);
            opacity: 0;
          }
          10% {
            opacity: 1;
          }
          90% {
            opacity: 1;
          }
          100% { 
            transform: translate(0, -100vh) scale(0.5);
            opacity: 0;
          }
        }

        @keyframes float-slow {
          0%, 100% { 
            transform: translateY(0px) translateX(0px);
          }
          25% {
            transform: translateY(-20px) translateX(10px);
          }
          50% {
            transform: translateY(-10px) translateX(-10px);
          }
          75% {
            transform: translateY(-30px) translateX(5px);
          }
        }

        @keyframes fade-in {
          from { opacity: 0; transform: translateY(20px); }
          to { opacity: 1; transform: translateY(0); }
        }

        @keyframes fade-in-up {
          from { opacity: 0; transform: translateY(40px); }
          to { opacity: 1; transform: translateY(0); }
        }

        @keyframes slide-in-left {
          from { opacity: 0; transform: translateX(-40px); }
          to { opacity: 1; transform: translateX(0); }
        }

        @keyframes slide-in-right {
          from { opacity: 0; transform: translateX(40px); }
          to { opacity: 1; transform: translateX(0); }
        }

        @keyframes scale-in {
          from { opacity: 0; transform: scale(0.9); }
          to { opacity: 1; transform: scale(1); }
        }

        @keyframes bounce-subtle {
          0%, 100% { transform: translateY(0); }
          50% { transform: translateY(-5px); }
        }

        @keyframes pulse-slow {
          0%, 100% { opacity: 1; }
          50% { opacity: 0.6; }
        }

        @keyframes ping-slow {
          0% {
            transform: scale(1);
            opacity: 1;
          }
          75%, 100% {
            transform: scale(2);
            opacity: 0;
          }
        }

        @keyframes gradient-x {
          0%, 100% {
            background-position: 0% 50%;
          }
          50% {
            background-position: 100% 50%;
          }
        }

        @keyframes gradient-y {
          0%, 100% {
            background-position: 50% 0%;
          }
          50% {
            background-position: 50% 100%;
          }
        }

        @keyframes dash {
          to {
            stroke-dashoffset: -20;
          }
        }

        .animate-float-particle { animation: float-particle linear infinite; }
        .animate-float-slow { animation: float-slow ease-in-out infinite; }
        .animate-fade-in { animation: fade-in 0.8s ease-out forwards; }
        .animate-fade-in-up { animation: fade-in-up 0.8s ease-out forwards; }
        .animate-slide-in-left { animation: slide-in-left 0.8s ease-out forwards; }
        .animate-slide-in-right { animation: slide-in-right 0.8s ease-out forwards; }
        .animate-slide-in-up { animation: fade-in-up 0.8s ease-out forwards; }
        .animate-scale-in { animation: scale-in 0.3s ease-out forwards; }
        .animate-bounce-subtle { animation: bounce-subtle 2s ease-in-out infinite; }
        .animate-pulse-slow { animation: pulse-slow 3s ease-in-out infinite; }
        .animate-ping-slow { animation: ping-slow 3s cubic-bezier(0, 0, 0.2, 1) infinite; }
        .animate-gradient-x { 
          background-size: 200% auto;
          animation: gradient-x 3s ease infinite;
        }
        .animate-gradient-y { 
          background-size: auto 200%;
          animation: gradient-y 3s ease infinite;
        }
        .animate-dash { 
          stroke-dasharray: 6 6;
          animation: dash 1s linear infinite;
        }

        /* Scroll-triggered animations */
        @media (prefers-reduced-motion: no-preference) {
          [class*="animate-"] {
            animation-fill-mode: both;
          }
        }
      `}</style>
    </main>
  );
}
